// ------------------- imports
import { onWindowResize } from 'utils';
// ------------------- imports###

// ------------------  import components
import { calcViewportHeight } from '../utils';

import 'ScssComponents/popup-sign-up.scss';
import 'ScssComponents/popup-form-sign-up.scss';
// ------------------  import components###

const dynamicLoad = async () => {
	const lazyLoad = await import('../components/lazy-load');
	lazyLoad.lazyLoad();
};

const layout = () => {
	onWindowResize(() => {
		calcViewportHeight();
	});
	calcViewportHeight();
	window.addEventListener('load', async () => {
		// const onloadStyles = await import('../components/onload-styles');

		const header = await import('../components/header');
		header.header();

		setTimeout(() => {
			// dynamicLoad();
		}, 900);

		const initPopup = await import('../components/popup-sign-up');
		initPopup.initPopup('.js-waitlist-btn', '.js-popup-sign-up');

		const disableEventOnScroll = await import('../components/disableEventOnScroll');
		disableEventOnScroll.disableEventOnScroll();
	});
};

export default layout;
